@keyframes shine {
  100% {
    left: -200%;
  }
}

@keyframes line {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes fade-up {
  from {
    opacity: 0;
    translate: 0 100%;
  }

  to {
    opacity: 1;
    translate: 0;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes scale-in {
  from {
    scale: .7;
  }

  to {
    scale: 1;
  }
}

.btn:after {
  content: "";
  background-image: linear-gradient(to right, #0000, #ffffff40, #0000);
  width: 200%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 150%;
  transform: skewX(-20deg);
}

.btn.btn--primary[disabled] {
  color: #b6b6b6;
  background-color: #f6f6f6;
  border-color: #f6f6f6;
}

.btn.btn--secondary[disabled] {
  color: #b6b6b6;
  background-color: #f6f6f6;
  border-color: #b6b6b6;
}

.btn.btn--tertiary[disabled] {
  color: #b6b6b6;
  border-color: #f6f6f6;
}

.btn.btn--primary:not([disabled]):not([loading]):hover:after {
  animation: 1s cubic-bezier(.01, .56, 1, 1) shine;
}

.btn.btn--tertiary:not([disabled]):not([loading])[hovered], .btn.btn--tertiary:not([disabled]):not([loading]):hover {
  border-color: var(--color);
}

.btn[loading] {
  color: var(--background-color);
}

.link {
  position: relative;
}

.link[disabled] {
  color: #b6b6b6;
}

.link:after {
  content: "";
  border-bottom: 1px solid var(--color-foreground);
  z-index: 1;
  width: 0%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.link:before {
  content: "";
  z-index: 0;
  border-bottom: 1px solid #b6b6b6;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.link:not([disabled]):not([loading])[hovered]:after, .link:not([disabled]):not([loading]):hover:after {
  animation: .3s cubic-bezier(.65, 0, .35, 1) both line;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6, .large, p, .p, small, .small, .btn {
  animation: fade-up view(90svh 0);
}

.image-wrapper > img, .image-wrapper > svg {
  animation: fade-in view(90svh 0);
}

:is(.image-wrapper > img, .image-wrapper > svg) > img, :is(.image-wrapper > img, .image-wrapper > svg) > svg {
  animation: scale-in view(90svh 0);
}

.form-element {
  flex-direction: column;
  gap: 10px;
  width: 100%;
  display: flex;
}

input, textarea {
  padding: 8px 10px;
}

.line-item .line-item-image {
  flex-basis: 140px;
}

.line-component .line-component-image {
  flex-basis: 60px;
}

.line-item-price-original {
  color: #b6b6b6;
  text-decoration: line-through;
}
